<template>
   <div class="wrapper">
      <div class="corridor">
        <img src="/img/blood.png" ref="blood" alt="blood" class="blood">
        <img src="/img/zombie.png"  alt="pic" class='zombie' @click="zoomZ">
      </div>
      <button class="next2" @click="toSpirit" v-show="flag">Дальше</button>
   </div>
</template>
<script>
export default {
  data() {
    return {
      flag:false,
    }
  },
  methods: {
    zoomZ() {
      let bl = this.$refs.blood;
      bl.style.display = 'block';
      this.flag = true;
    },
    toSpirit() {
      this.$router.push('/level4')
    }
  }
}
</script>
<style scoped>
   @font-face {
    font-family: 'horror';
    src: url('/fonts/ofont.ru_ds_eraser2-webfont.woff2') format('woff2'),
        url('/fonts/ofont.ru_ds_eraser2-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;

  }

  .wrapper {
    width: 600px;
    margin: 50px auto;
  }
  .corridor {
    width:100%;
    height: 400px;
    background-image:url('/img/corridor.jpg');
    background-repeat:no-repeat;
    background-size:100% 100%;
    position: relative;
  }
  .zombie {
    width: 100px;
    position: relative;
    top:100px;
    left:50%;
    margin-left:-50px;
    cursor:pointer;
  }
  .blood {
    position: absolute;
    left:100px;
    top:100px;
    z-index:1;
    display:none;
    height: 300px;
  }

  .next2 {
    font-family:horror;
    color:red;
    cursor: pointer;
    font-size:25px;
    display: block;
    text-align: center;
    width:120px;
    margin:20px auto;
  }

</style>


